body {
  background-color: #C8D0D2;
  margin:0
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  background-color: white;
  padding: 0 1.5rem;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .3);
}

select{
  padding:1%
}

.form-group {
  display: flex;
  flex-direction: column;
  padding: 2px 2px;
  margin: .5rem;
}

.form-group > label {
  color: rgb(0, 0, 0);
  font-size: .75rem;
  margin-bottom: .25rem;
}

.btn {
  background-color: hsl(24, 100%, 50%);
  color: white;
  padding: .5em 1em;
  border-radius: .3em;
  border: none;
  cursor: pointer;
}

.btn:hover {
  background-color: hsl(37, 100%, 40%);
}

.container{
  max-width: 100vw;
  margin:1rem 2rem;
}

.card-grid{
  display:grid;
  align-items:center;
  grid-template-columns: repeat(auto-fill,minmax(300px,1.5fr));
  gap:1rem;
}
.card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: .25rem;
  box-shadow: 0 0 5px 2px rgba(0,0,0,.3);
  background-color:white;
  transform-style: preserve-3d;
  

  transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
  transition:150ms;
  cursor: pointer;
 


}

.card:hover {
  --translate-y: -2px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .5);
}

.card.flip{
  --rotate-y: 180deg;
}
.card .front,
.card .back{
  position: absolute;
  padding: 1rem;
  backface-visibility: hidden;

}

.card .front{
  left: 0;
}


.card .back{
  transform:rotateY(180deg);
  color:black
}


.flashcard-options{
  margin-top: 0.5rem;
}

.flashcard-option{
  margin-top: 0.25rem;
  color: #555;
  font-size: .75rem;
}

.flashcard-option:first-child{
  margin-top: 0;
}